.promo {
  background-color: $basic-yellow;

  @media (max-width: $mobile-width) {
    min-height: 460px;
  }
}

.promo__wrapper {
  padding-top: 104px;
  padding-bottom: 95px;
  position: relative;

  @media (max-width: $tablet-width) {
    padding-top: 60px;
  }

  @media (max-width: $mobile-width) {
    padding-top: 20px;
  }
}

.promo__title {
  color: $basic-white;
  margin: 0;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 144px;
  line-height: 152px;
  margin-bottom: 31px;
  position: relative;
  z-index: 1;

  @media (max-width: $tablet-width) {
    font-size: 90px;
    line-height: 100px;
  }

  @media (max-width: $mobile-width) {
    font-size: 40px;
    line-height: 40px;
  }
}

.promo__paragraph {
  font-size: 24px;
  line-height: 28px;
  max-width: 650px;
  margin: 0;
  margin-bottom: 109px;

  @media (max-width: $tablet-width) {
    font-size: 18px;
    line-height: 21px;
    max-width: 490px;
    margin-bottom: 90px
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 30px
  }
}

.promo__sup-title {
  font-weight: bold;
  font-size: 36px;
  line-height: 43px;
  margin: 0;
  margin-bottom: 8px;
  text-transform: uppercase;

  @media (max-width: $tablet-width) {
    font-size: 30px;
    line-height: 36px;
  }
}

.promo__sup-paragraph {
  font-size: 18px;
  line-height: 21px;
  margin: 0;
  max-width: 350px;

  @media (max-width: $mobile-width) {
    max-width: 180px;
  }
}

.promo__img-box {
  position: absolute;
  top: -80px;
  right: -48px;
  z-index: 0;

  @media (max-width: $tablet-width) {
    max-width: 600px;
    top: -40px;
  }

  @media (max-width: $mobile-width) {
    max-width: 370px;
    top: 130px;
  }
}

.promo__wrapper *:not(.promo__img-box) {
  position: relative;
  z-index: 1;
}
